.container {
    width: 100%;
    padding: 0 15px;
    margin: 0 auto;
  }
  
  @media screen and (min-width: 480px) {
    .container {
      width: 480px;
    }
  }
  
  @media screen and (min-width: 768px) {
    .container {
      width: 768px;
    }
  }
  
  @media screen and (min-width: 1280px) {
    .container {
      width: 1080px;
    }
  }
  